.bag {
    width: 52.5vw;
    height: 100%;
    margin: 0 auto;
    padding-top: 2.5vh;
    display: none;
    position: absolute;
    right: 0;
    top: 0;
    background-color: white;
}

.open {
    display: block;
    z-index: 25;
    box-shadow: 0 0 0 9999px rgba(0, 0, 0, 0.6);
}

.disable__pointers {
    pointer-events: none;
    user-select: none;
}

.disable__scroll {
    overflow: hidden;
}

.bag__top__bar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    width: 87.5%;
    margin: 0 auto 7.5vh;
}

.bag__top__bar h1 {
    font-size: 2.5rem;
    font-weight: 600;
}

.bag__top__bar p {
    font-size: 2rem;
    font-weight: 600;
    cursor: pointer;
    color: rgb(77, 76, 76);
}

.bag__products {
    width: 85%;
    margin: 0 auto;
    color: gray;
    font-size: 1.2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem 0.75rem;
    border-bottom: 2px solid lightgray;
}

.bag__checkout {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 90%;
    margin-top: 7vh;
}

.bag__checkout p {
    color: gray;
    font-size: 1.3rem;
    padding: 1rem 0;
}

.bag__checkout p span {
    color: #000;
}

.bag__checkout button {
    background: #000;
    color: #fff;
    border: 2px solid #000;
    width: auto;
    padding: 0.75rem 2.75rem;
    font-size: 1.15rem;
    cursor: pointer;
    transition: .25s;
}

.bag__checkout button:hover {
    background: #fff;
    color: #000;
}

#close__icon {
    cursor: pointer;
    font-size: 35px;
    color: rgb(95, 95, 95);
    transition: .125s;
}

#close__icon:hover {
    color: #000;
}