.product__container {
    width: 70vw;
    margin: 5vh auto;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-direction: row;
}


/*  IMAGE  */

.product__image {
    background: url(https://images.unsplash.com/photo-1504051771394-dd2e66b2e08f?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=50) 50% 70%;
    width: 30vw;
    height: 80vh;
    background-size: cover;
}


/* ! ! ! */

.product {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    left: 30%;
    display: contents;
}

.product-small-img {
    float: left;
}

.product-small-img img {
    height: 92px;
    opacity: 0.6;
    /* test */
    height: 120px;
    margin: 15px 0;
    cursor: pointer;
    display: block;
    /*width: 90%;*/
    width: 100%;
    object-fit: cover;
}

.product-small-img img:first-of-type {
    margin-top: 0;
}

.product-small-img img:hover {
    opacity: 1;
}

.img-container {
    float: right;
}

.img-container img {
    height: 500px;
    height: 70vh;
    min-width: 90%;
    max-width: 90%;
    width: 30vw;
    object-fit: cover;
    object-position: bottom;
    /* or center */
}


/* ! ! ! */

.product__info {
    display: flex;
    flex-direction: column;
    width: 40%;
}

ol {
    display: flex;
    justify-content: space-between;
    list-style: disc;
    width: 85%;
    margin-bottom: 7.5%;
}

ol li:first-of-type {
    list-style: none;
}

ol li {
    cursor: pointer;
}

ol li.active {
    text-decoration-line: underline;
    text-decoration-thickness: 2px;
}

.product__description {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.product__description h1 {
    font-size: 37px;
    margin-bottom: 2.5%;
}

.product__description p {
    font-size: 21px;
}

.quantity__selector {
    display: flex;
    align-items: center;
    margin-top: 5%;
}

.quantity__selector p {
    width: 75px;
}

.quantity__selector input {
    border: 0;
    outline: 0;
    background-color: #dadada;
    padding: 2px 2px 2px 15px;
    width: 50px;
    border-radius: 10px;
}

.product__button {
    background-color: #fff;
    border: 2px solid #000;
    width: auto;
    padding: 0.75rem 2.75rem;
    font-size: 1.15rem;
    cursor: pointer;
    transition: .25s;
    width: fit-content;
    margin: 10% 0;
    transition: .225s;
}

.product__button:hover {
    box-shadow: inset 0 -3.25em 0 0 #000;
    color: #fff;
}

.product__info form {
    font-size: 15px;
    font-weight: 400;
}

.size__select {
    display: flex;
    align-items: center;
    margin-top: 7.5%;
}

.size__select p {
    width: 60px;
    font-size: 16px;
}

.size__select input {
    display: none;
}

.size__select label {
    margin: 0 5px;
}

.size__select span {
    height: 32px;
    width: 32px;
    margin: 0 5%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: gray;
}

.size__select input:checked+span {
    color: #000;
    border: 2px solid #000;
    font-weight: bold;
}